import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import M from "materialize-css";
import { addTodo, deleteTodo, removeTodo } from "../actions/index";

export const Body = () => {
  const [inputData, setInputData] = useState("");
  const dispatch = useDispatch();
  const list = useSelector((state) => {
    return state.todoReducers.list;
  });
  return (
    <div className="body">
      <div className="add-item">
        <div class="row">
          <form class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <textarea
                  id="textarea1"
                  class="materialize-textarea"
                  value={inputData}
                  onChange={(e) => {
                    e.preventDefault();
                    setInputData(e.target.value);
                  }}
                ></textarea>
                <label for="textarea1">Add Tasks</label>
              </div>
            </div>
          </form>
        </div>
        <a class="btn-floating btn-medium waves-effect waves-light #6a1b9a purple darken-3">
          <i
            class="material-icons"
            title="add item"
            onClick={() => {
              inputData.trim() == ""
                ? M.toast({
                    html: "Task cannot be empty !!",
                    classes: "rounded, red",
                    displayLength: 2000,
                    outDuration: 500,
                  })
                : dispatch(addTodo(inputData.trim()));
              setInputData("");
            }}
          >
            add
          </i>
        </a>
      </div>

      <button
        class="btn waves-effect waves-light red"
        type="submit"
        name="action"
        onClick={() => {
          dispatch(removeTodo());
        }}
      >
        Remove All
        <i class="material-icons right">delete</i>
      </button>

      <div className="show-item">
        {list.map((ele) => {
          return (
            <div class="row" key={ele.id}>
              <div class="col s12 m12">
                <div class="card #6a1b9a purple darken-3">
                  <div class="card-image">
                    <a class="btn-floating halfway-fab waves-effect waves-light red">
                      <i
                        class="material-icons"
                        title="add item"
                        onClick={() => {
                          dispatch(deleteTodo(ele.id));
                        }}
                      >
                        delete
                      </i>
                    </a>
                  </div>
                  <div class="card-content">
                    <p>{ele.data}</p>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
